import React from "react";
import DisLike from "assets/svg/DisLike";
import Like from "assets/svg/Like";
import Bookmark from "assets/svg/Bookmark";

// Title, description/article, image and tags
function ItemCard(props) {
  const { item } = props;
  let {
    title = "",
    description = "",
    url = "",
    urlToImage = "",
    publishedAt = "",
    content = "",
    source: { name = "" }
  } = item;
  let index = content && content.indexOf("…");
  if (index && index !== -1) {
    content = content.slice(0, index + 1);
  }

  return (
    <div className="ItemCard center">
      <div className="ItemCard_detail">
        {/* <a href={url} target="_blank" rel="noopener noreferrer"> */}
        <h5 className="marginPadding">{title}</h5>
        {/* </a> */}
        <div>
          <b>Short by</b>
          {` ${name} at ${publishedAt}`}
        </div>
        <div className="content desc_truncate">{content}</div>
        <div className="center ItemCard_actions">
          <div className="item">
            <Like className="active" />
          </div>
          <div className="item">
            <DisLike />
          </div>
          <div className="item">
            <Bookmark />
          </div>
        </div>
      </div>
      {urlToImage ? (
        <div className="ItemCard_imageView">
          <img src={urlToImage} alt="img" />
        </div>
      ) : (
        void 0
      )}
    </div>
  );
}

export default ItemCard;
